import { toast } from "react-toastify";

export const TosterSuccess = (props) => {
  return toast.success(props, {
    position: "top-right",
    autoClose: 5000,
    hideProgressBar: false,
    closeOnClick: false,
    pauseOnHover: false,
    draggable: false,
    progress: undefined,
    theme: "dark",
    });
}
