import React from 'react'

export const Heading = (props) => {
    return (
        <div>
            <section className="inner-banner bg_img Heading-Background " >
                <div className="container">
                    <div className="inner-banner-content">
                        <h2 className="title">{props.title}</h2>
                    </div>
                </div>
            </section>
        </div>
    )
}
