import React from 'react'
import pandaLogo from '../Assets/images/panda.png'


export const NevbarX = () => {
    return (
        <div>


            {/* Navbar */}
            <nav className="navbar navbar-expand navbar-light px-1 px-sm-3 bg-x1 shadow-0">
                {/* Container wrapper */}
                <div className="container-fluid">
                    {/* Toggle button */}
                    <div class="dropdown d-lg-none">
                        <a
                            class=""
                            href="#"
                            role="button"
                            id="dropdownMenuLink"
                            data-mdb-toggle="dropdown"
                            aria-expanded="false"
                        >
                            <i class="fa-solid fa-bars text-dark me-3 menux"></i>
                        </a>

                        <ul class="dropdown-menu" aria-labelledby="dropdownMenuLink">
                            <li><a class="dropdown-item" href="#">Action</a></li>
                            <li><a class="dropdown-item" href="#">Another action</a></li>
                            <li><a class="dropdown-item" href="#">Something else here</a></li>
                        </ul>
                    </div>
                    {/* Collapsible wrapper */}
                    <div className="collapse navbar-collapse justify-content-lg-center justify-content-start d-flex" id="navbarSupportedContent">
                        {/* Navbar brand */}
                        <a className="navbar-brand" href="#">
                            <img src={pandaLogo} height={40} alt="MDB Logo" loading="lazy" />
                        </a>
                        {/* Left links */}
                        <ul className="navbar-nav  m-auto d-none d-lg-flex me-0" >
                            <li className="nav-item">
                                <a className="nav-link" href="#">Dashboard</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" href="#">Team</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" href="#">Projects</a>
                            </li>
                        </ul>
                        {/* Left links */}
                    </div>
                    {/* Collapsible wrapper */}
                    {/* Right elements */}
                    <div className="d-flex align-items-center d-none">
                        {/* Icon */}
                        <a className="text-reset me-3" href="#">
                            <i className="fas fa-shopping-cart" />
                        </a>
                        {/* Notifications */}
                        <div className="dropdown">
                            <a className="text-reset me-3 dropdown-toggle hidden-arrow" href="#" id="navbarDropdownMenuLink" role="button" data-mdb-toggle="dropdown" aria-expanded="false">
                                <i className="fas fa-bell" />
                                <span className="badge rounded-pill badge-notification bg-danger">1</span>
                            </a>
                            <ul className="dropdown-menu dropdown-menu-end" aria-labelledby="navbarDropdownMenuLink">
                                <li>
                                    <a className="dropdown-item" href="#">Some news</a>
                                </li>
                                <li>
                                    <a className="dropdown-item" href="#">Another news</a>
                                </li>
                                <li>
                                    <a className="dropdown-item" href="#">Something else here</a>
                                </li>
                            </ul>
                        </div>
                        {/* Avatar */}
                        <div className="dropdown">
                            <a className="dropdown-toggle d-flex align-items-center hidden-arrow" href="#" id="navbarDropdownMenuAvatar" role="button" data-mdb-toggle="dropdown" aria-expanded="false">
                                <img src="https://mdbcdn.b-cdn.net/img/new/avatars/2.webp" className="rounded-circle" height={25} alt="Black and White Portrait of a Man" loading="lazy" />
                            </a>
                            <ul className="dropdown-menu dropdown-menu-end" aria-labelledby="navbarDropdownMenuAvatar">
                                <li>
                                    <a className="dropdown-item" href="#">My profile</a>
                                </li>
                                <li>
                                    <a className="dropdown-item" href="#">Settings</a>
                                </li>
                                <li>
                                    <a className="dropdown-item" href="#">Logout</a>
                                </li>
                            </ul>
                        </div>
                    </div>
                    {/* Right elements */}
                </div>
                {/* Container wrapper */}
            </nav>
            {/* Navbar */}
        </div>
    )
}
