import React, { useState } from 'react'
import russian from '../Assets/images/user.jpg'
import { useContext } from 'react'
import { SingalProductContext } from '../Contexts/SingalX'

export const SingalProduct = (props) => {
    
    const FetchProduct = useContext(SingalProductContext);

  

  return (
    <div className=''>
    
       
        <div className="BG-fix">
            <div className="bg-card">
                <div className="bg-card-x"></div>
                <div className="main-card">
                    <div className="card h-100 w-100">
                        <div className="row row-cols-1 row-cols-md-2">
                            <div className="col p-4 px-5 d-flex">
                                <img src={FetchProduct.Dproduct.image} className='w-100X card shadow-0 m-auto' alt="" />
                            </div>
                            <div className="col d-flex flex-column">
                            <div className='d-flex'><button className='btn shadow-0 btn-x1 ms-auto m-4 btn-sm p-0 close-x1' onClick={()=>{props.revPro()}}>X</button></div>
                            <div className="m-auto mt-4 py-4 w-100 pe-2">
                               
                                <div className=''>
                                    <p className='font-x1 mb-0'><span className='label-x'>Category : </span>{FetchProduct.Dproduct.category}</p>
                                </div>
                                <div className='mt-2'>
                                    <p className='font-x2 mb-0'><span className='label-x'>Title : </span>{FetchProduct.Dproduct.title}</p>
                                </div>
                              
                                <div className="d-flex justify-content-between flex-wrap w-75">
                                <div className='mt-2'>
                                    <p className='font-x2 mb-0'><span className='label-x'>Price : </span>{FetchProduct.Dproduct.price}</p>
                                </div>
                                <div className='mt-2'>
                                    <p className='font-x2 mb-0'><span className='label-x'>Count : </span>{FetchProduct.Dproduct.rating.count}</p>
                                </div>
                                </div>
                                <div className='mt-2'>
                                    <p className='font-x2 mb-0'><span className='label-x'>Description : </span>{FetchProduct.Dproduct.description}</p>
                                </div>
                            </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    
       
        
    </div>
  )
}
