import React, { useContext } from "react";
import './Assets/css/Global.css'
import { Route, Routes } from "react-router-dom";
import { Login } from "./Pages/Login";
import { Registration } from "./Pages/Registration";
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { Error404 } from "./Pages/Error404";
import { Home } from "./Pages/Home";
import { ProductContext } from "./Contexts/ProductContext";
import { Provider } from "react-redux";
import { MainStore } from "./Store/MainStore";
import { AddToCart } from "./Pages/AddToCart";
import { About } from "./Pages/About";
import { TermsAndCondition } from "./Pages/TermsAndCondition";
import { Shop } from "./Pages/Shop";
import { TostContainerX } from "./Components/TostContainerX";
import { ForgotPassword } from "./Pages/ForgotPassword";
import { ResetPassword } from "./Pages/ResetPassword";
import { Admin } from "./Pages/Admin";
// import { Idgenrator } from "./Components/Idgenrator";


// import nextId from "react-id-generator/lib/nextId";



const theme = createTheme({
  palette: {
    backgroundX: {
      // Purple and green play nicely together.
      main: ' rgb(229, 229, 229)',
      color: 'red',

    },

  },
});

function App() {

  const ProductA = useContext(ProductContext);
  return (
    <ProductContext.Provider value={{
      ...ProductA,
      empty: "empty"
    }}>


      <Provider store={MainStore}>
        <ThemeProvider theme={theme}>
          <Routes>
            <Route path="/" element={<Login />} />
            <Route path="/registration" element={<Registration />} />
            <Route path="*" element={<Error404 />} />
            <Route path="/forgot" element={<ForgotPassword />} />
            <Route path="/reset" element={<ResetPassword />} />
            <Route path="/home" element={<Home />} />
            <Route path="/home/addtocart" element={<AddToCart />} />
            <Route path="/about" element={<About />} />
            <Route path="/Terms" element={<TermsAndCondition />} />
            <Route path="/shop" element={<Shop />} />
            <Route path="/admin" element={<Admin />} />
          </Routes>
        </ThemeProvider>
      </Provider>

      <TostContainerX/>

{/* Same as */}
    </ProductContext.Provider>
  );
}

export default App;
