import React, { useState } from 'react'
import { NevbarY } from '../Components/NevbarY.jsx'

import { FooterY } from '../Components/FooterY.jsx'
import { SingalProduct } from './SingalProduct.jsx'
import { SingalProductContext } from '../Contexts/SingalX.jsx'
import { CartProducts } from '../Components/CartProducts.jsx'
import { Heading } from '../Components/Heading.jsx'
import { Nodata } from '../Components/Nodata.jsx'
import { useSelector } from 'react-redux'



export const AddToCart = () => {
  
  const CartData = useSelector((state)=>{return state.Products.ProductCart});

  const [hPro, sethPro] = useState(false);
  const [Dproduct,setDproduct] = useState({})

  const revPro = () => { sethPro(!hPro) };

  return (
    <div>
      <NevbarY />
     <Heading title={"Add To Cart"}/>
      <SingalProductContext.Provider value={{
           Dproduct:Dproduct,
           setDproduct:setDproduct,
      }}>
       
         {CartData.length !==0?<CartProducts revPro={revPro} />:<Nodata />}
        {hPro && <SingalProduct revPro={revPro} />}

     
      </SingalProductContext.Provider>
    
      <FooterY />
    </div>
  )
}
