import React from 'react'
import Slider1 from '../Assets/images/s1.webp';
import Slider2 from '../Assets/images/s2.webp';

export const Cousel = () => {
  return (
    <div>
         <div id="carouselExampleTouch" className="carousel slide" data-mdb-touch="false">
          <div className="carousel-indicators">
            <button type="button" data-mdb-target="#carouselExampleTouch" data-mdb-slide-to={0} className="active" aria-current="true" aria-label="Slide 1" />
            <button type="button" data-mdb-target="#carouselExampleTouch" data-mdb-slide-to={1} aria-label="Slide 2" />
           
          </div>
          <div className="carousel-inner">
            <div className="carousel-item active">
              <img src={Slider1} className="d-block w-100" alt="Wild Landscape" />
              <div className="carousel-caption d-none d-md-block">
                <h5>First slide label</h5>
                <p>Nulla vitae elit libero, a pharetra augue mollis interdum.</p>
              </div>
            </div>
            <div className="carousel-item">
              <img src={Slider2} className="d-block w-100" alt="Camera" />
              <div className="carousel-caption d-none d-md-block">
                <h5>Second slide label</h5>
                <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit.</p>
              </div>
            </div>
            
          </div>
          <button className="carousel-control-prev" type="button" data-mdb-target="#carouselExampleTouch" data-mdb-slide="prev">
            <span className="carousel-control-prev-icon" aria-hidden="true" />
            <span className="visually-hidden">Previous</span>
          </button>
          <button className="carousel-control-next" type="button" data-mdb-target="#carouselExampleTouch" data-mdb-slide="next">
            <span className="carousel-control-next-icon" aria-hidden="true" />
            <span className="visually-hidden">Next</span>
          </button>
        </div>
      </div>
  
  )
}
