import React, { useState } from 'react';
import ForgotPasswordImage from '../Assets/images/forpass.svg';
import { Link, useNavigate } from 'react-router-dom';
import { useDispatch, } from 'react-redux';
import { ProductAction } from '../Store/ProductSlice';

export const ForgotPass = () => {
  const DISPATCH = useDispatch();
  const redirection = useNavigate();
  const initialUser2= {
    email:"",
  }

  const [ForgotUser,setForgotUser]=useState(initialUser2);

  const setLoginData=(e)=>
  {
    setForgotUser({...ForgotUser,[e.target.name]:e.target.value});
  }

  const LoginSubmitData=(e)=>
  {
    e.preventDefault();
    DISPATCH(ProductAction.UserForgotPass({value:ForgotUser,redirection:redirection}));
    setForgotUser(initialUser2);
  }

  // console.log(">>>>>>>>>",ForgotUser);
  return (
    <div>
         <div className="container my-4">
          <div className="card">
          <section className="">
        <div className="container  ">
          <div className="row d-flex align-items-center justify-content-center  ">
            <div className="col-md-8 col-lg-7 col-xl-6 ">
              <img src={ForgotPasswordImage} className="img-fluid" alt="Phone image" />
            </div>
            <div className="col-md-7 col-lg-5 col-xl-6 px-3 px-md-5 ps-xl-3">
              <p className='tit-x mb-3 e-title'>Forgot Password</p>
              <form onSubmit={(e)=>{LoginSubmitData(e)}}>
                {/* Email input */}
                <div className=" mb-4">
                  <input type="email" id="form1Example13" className="form-control form-control-lg" placeholder='EMAIL' name="email" value={ForgotUser.email} onChange={(e)=>{setLoginData(e)}}/>
                 
                </div>
               

                {/* Submit button */}
                <button type="submit" className="btn btn-x1  btn-lg btn-block">Sign in</button>
                <div className="d-flex mt-4 justify-content-around"><Link to="/registration" className='color-x1'>don't have an account ?</Link><Link to="/" className='color-x1'>Back to Login?</Link></div>
                <div className="divider d-flex align-items-center my-3">
                  <p className="text-center fw-bold mx-3 mb-0 text-muted">OR</p>
                </div>
                <a className="btn btn-primary btn-lg btn-block shadow-0" style={{backgroundColor: '#3b5998'}} href="#!" role="button">
                  <i className="fab fa-facebook-f me-2 " />Continue with Facebook
                </a>
                <a className="btn btn-primary btn-lg btn-block shadow-0" style={{backgroundColor: '#55acee'}} href="#!" role="button">
                  <i className="fab fa-twitter me-2" />Continue with Twitter</a>
              </form>
            </div>
          </div>
        </div>
      </section>
          </div>
         </div>
    </div>
  )
}
