import React, { useState } from 'react';
import ResetPasswordImage from '../Assets/images/reset.svg';
import { Link, useNavigate } from 'react-router-dom';
import { useDispatch, } from 'react-redux';
import { ProductAction } from '../Store/ProductSlice';

export const ResetPass = () => {
  const DISPATCH = useDispatch();
  const redirection = useNavigate();
  const initialUser3= {
    firstPass:"",
    secondPass:"",
  }

  const [ResetUser,setResetUser]=useState(initialUser3);

  const setLoginData=(e)=>
  {
    setResetUser({...ResetUser,[e.target.name]:e.target.value});
  }

  const LoginSubmitData=(e)=>
  {
    e.preventDefault();
    DISPATCH(ProductAction.UserResetPass({value:ResetUser,redirection:redirection}));
    setResetUser(initialUser3);
  }

  // console.log(">>>>>>>>>",ResetUser);
  return (
    <div>
         <div className="container my-4">
          <div className="card">
          <section className="">
        <div className="container  ">
          <div className="row d-flex align-items-center justify-content-center  ">
            <div className="col-md-8 col-lg-7 col-xl-6 ">
              <img src={ResetPasswordImage} className="img-fluid" alt="Phone image" />
            </div>
            <div className="col-md-7 col-lg-5 col-xl-6 px-3 px-md-5 ps-xl-3">
              <p className='tit-x mb-3 e-title'>Reset Password</p>
              <form onSubmit={(e)=>{LoginSubmitData(e)}}>
                {/* Email input */}
                <div className=" mb-4">
                  <input type="password" id="form1Example13" className="form-control form-control-lg" placeholder='Enter Password' name="firstPass" value={ResetUser.firstPass} onChange={(e)=>{setLoginData(e)}}/>
                 
                </div>
                 {/* Email input */}
                 <div className=" mb-4">
                  <input type="password" id="form1Example13" className="form-control form-control-lg" placeholder='Conform Password' name="secondPass" value={ResetUser.secondPass} onChange={(e)=>{setLoginData(e)}}/>
                 
                </div>
               

                {/* Submit button */}
                <button type="submit" className="btn btn-x1  btn-lg btn-block">Reset</button>
               
              </form>
            </div>
          </div>
        </div>
      </section>
          </div>
         </div>
    </div>
  )
}
