import React, { useEffect, useState } from 'react'
import Regi from '../Assets/images/regi.svg'
import { Link, Navigate, useNavigate } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import { ProductAction } from '../Store/ProductSlice';
import { TosterSuccess } from './TostSuccess';


export const RegiForm = () => {
  

  const initialUser = 
   {
    firstname:"",
    lastname:"",
    address:"",
    states:1,
    city:1,
    DOB:"",
    mono:"",
    email:"",
    password:"",
   }

  const [userRegi,setUserRegi] = useState(initialUser);


  const DISPATCH = useDispatch();
  const redirection = useNavigate();

  const userDataInput = (event) =>
  {
    setUserRegi({...userRegi,[event.target.name]:event.target.value})
  }

  // console.log(userRegi);

  const forSubmit=(e)=>
  {
    e.preventDefault();
    DISPATCH(ProductAction.UserRegistration({value:userRegi,redirection:redirection}));
  }

  return (
    <div>
        <div className="container my-4">
             <div className="card">
             <div className="row d-flex justify-content-center align-items-center">
            <div className="col">
              <div className="card card-registration">
                <div className="row g-0">
                  <div className="col-xl-6 d-none d-xl-flex ps-md-3">
                  <img src={Regi} alt=""  className='img-fluid w-100 m-auto'/>
                  </div>
                  <div className="col-xl-6">
                    <div className="card-body p-md-5 ps-xl-0 text-black">
                    <p className='tit-x mb-3 e-title'>Registration</p>


                    <form action="" onSubmit={(e)=>{forSubmit(e)}}>
                      <div className="row">
                        <div className="col-md-6 mb-4">
                          <div className="">
                            <input type="text" id="form3Example1m" className="form-control form-control-lg"  placeholder='firstname' value={userRegi.firstname} name='firstname' required onChange={(event)=>{userDataInput(event)}}/>
                          </div>
                        </div>
                        <div className="col-md-6 mb-4">
                          <div className="">
                            <input type="text" id="form3Example1n" className="form-control form-control-lg"value={userRegi.lastname} placeholder='lastname'name='lastname' onChange={(event)=>{userDataInput(event)}}required />
                          </div>
                        </div>
                      </div>
                     
                      <div className=" mb-4">
                        <input type="text" id="form3Example8" className="form-control form-control-lg" placeholder='Address' name='address'value={userRegi.address} onChange={(event)=>{userDataInput(event)}} required/>
                       
                      </div>
                     
                      <div className="row">
                        <div className="col-md-6 mb-4">
                          <select className="select form-control form-control-lg" name='states' value={userRegi.states} onChange={(event)=>{userDataInput(event)}} required>
                            <option value={1} disabled selected>State</option>
                            <option value={"gujarat"}>gujarat</option>
                            <option value={"maharastra"}>maharastra</option>
                            <option value={"rajsthan"}>rajsthan</option>
                          </select>
                        </div>
                        <div className="col-md-6 mb-4">
                          <select className="select form-control form-control-lg"name='city' value={userRegi.city} onChange={(event)=>{userDataInput(event)}} required>
                            <option value={1} disabled selected>City</option>
                            <option value={"surat"}>surat</option>
                            <option value={"vapi"}>vapi</option>
                            <option value={"valsad"}>valsad</option>
                          </select>
                        </div>
                      </div>
                      <div className=" mb-4">
                        <input type="date" id="form3Example9" className="form-control form-control-lg" placeholder='Date of birth' name='DOB'value={userRegi.DOB} onChange={(event)=>{userDataInput(event)}} required/>
                        
                      </div>
                      <div className=" mb-4">
                        <input type="number" id="form3Example90" className="form-control form-control-lg" placeholder='Mo number' name='mono'value={userRegi.mono} onChange={(event)=>{userDataInput(event)}}required/>
                      
                      </div>
                     
                      <div className=" mb-4">
                        <input type="email" id="form3Example97" className="form-control form-control-lg" placeholder='Email'name='email'value={userRegi.email} onChange={(event)=>{userDataInput(event)}} required/>
                       
                      </div>
                      <div className=" mb-4">
                        <input type="password" id="form3Example97" className="form-control form-control-lg" placeholder='Password'name='password'value={userRegi.password} onChange={(event)=>{userDataInput(event)}}required/>
                       
                      </div>

                      <div className="d-flex">
                        <Link to="/" className="color-x1 m-auto">I have an account ?</Link>
                      </div>

                      <div className="d-flex justify-content-end pt-3">
                        <button type="reset" className="btn btn-bg-x1 btn-lg">Reset all</button>
                        <button type="submit" className="btn btn-x1 btn-lg ms-2">Submit form</button>
                      </div>
                      </form>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
             </div>
        </div>
    </div>
  )
}
