import { createSlice } from '@reduxjs/toolkit'
import { TosterSuccess } from '../Components/TostSuccess';
import { TosterError } from '../Components/TostError';
import { Idgenrator } from '../Components/Idgenrator';

const getStore = (key) => {
    return JSON.parse(localStorage.getItem(key)) || [];
}

const initialState = {
    ProductCart: [],
    UserRegistrationData: getStore("UserRegistrationData"),
    hasUser: false,
    findUser: false,
    findEmail: false,
    ForgotEmail: "",
    FindUserObject: "",
    isAdmin: false,
    UserIdForCart:null

    //  text:"null"
}

export const ProductSlice = createSlice({

    name: "Products",
    initialState: initialState,
    reducers: {
        // this is the scope of the reducers //
        setStore: (state, action) => {
            const ObjVal = { ...action.payload };

            switch (ObjVal.key) {
                case "ProductCart":
                    state.ProductCart = [...state.ProductCart, ObjVal.value];
                    localStorage.setItem(state.UserIdForCart, JSON.stringify(state.ProductCart));
                    break;


                default: console.log("somthing wrong in storage key !");
            }

        },
        updateCart: (state, action) => {
            const ProductId = { ...action.payload };
            const UpdateCartData = state.ProductCart.filter((value, index) => {
                if (ProductId.key !== value.id) {
                    return value;
                }
            });
            state.ProductCart = [...UpdateCartData];
            localStorage.setItem(state.UserIdForCart, JSON.stringify([...state.ProductCart]));
        },

        UserRegistration: (state, action) => {

            let unloadUser = { ...action.payload };

            state.UserRegistrationData.map((value) => {
                if (unloadUser.value.email == value.email) {
                    state.hasUser = true;
                }
            })

            if (!state.hasUser) {
                state.UserRegistrationData = [...state.UserRegistrationData, {...unloadUser.value,UserId:Idgenrator()}];
                localStorage.setItem("UserRegistrationData", JSON.stringify([...state.UserRegistrationData]));
                TosterSuccess("register successfully !");
                unloadUser.redirection("/")
            }
            else if (state.hasUser) {
                state.hasUser = false;
                TosterError("this email id is already exsist !");

            }

        },
        UserLogin: (state, action) => {
            const unloadUser = { ...action.payload };

            state.UserRegistrationData.filter((value) => {
                if (unloadUser.value.email == "prince123@gmail.com" && unloadUser.value.password == "123123") {
                    state.isAdmin = true;

                } else if (unloadUser.value.email !== "prince123@gmail.com" || unloadUser.value.password !== "123123") {
                    state.isAdmin = false;
                }

            });
            // console.log(state.isAdmin);
            if (state.isAdmin) {
                unloadUser.redirection("/admin");
                TosterSuccess("welcome prince !");
                state.isAdmin = false;
            } else if (!state.isAdmin) {
                state.UserRegistrationData.map((value) => {
                    if (value.email == unloadUser.value.email && value.password == unloadUser.value.password) {
                        let spredUser = {...value};
                        state.findUser = true;
                        state.UserIdForCart = spredUser.UserId;
                        state.ProductCart = [...getStore(spredUser.UserId)];
                    }
                });
                if (state.findUser) {
                    TosterSuccess("login successfully !");
                    unloadUser.redirection("/home");
                } else if (!state.findUser) {
                    TosterError("wrong email or password")
                }

            }
            state.findUser = false;
            state.isAdmin = false;

        },

        UserForgotPass: (state, action) => {
            const unloadUser = { ...action.payload };

            state.UserRegistrationData.map((value) => {
                if (value.email == unloadUser.value.email) {
                    state.findEmail = true;
                }
            });

            if (state.findEmail) {
                TosterSuccess("email match !");
                state.ForgotEmail = `${unloadUser.value.email}`;
                unloadUser.redirection("/reset");
            }
            else if (!state.findEmail) {
                TosterError("email mot match !")
            }
            state.findEmail = false;
        },
        
        UserResetPass: (state, action) => {
            let unloadUser = { ...action.payload };
            if (unloadUser.value.firstPass == unloadUser.value.secondPass) {

                let change = state.UserRegistrationData.filter((value) => {
                    if (value.email !== state.ForgotEmail) {
                        return value;
                    } else if (value.email == state.ForgotEmail) {
                        state.FindUserObject = value;
                    }
                });

                const oprationUser = { ...state.FindUserObject, password: unloadUser.value.firstPass };
                const finalPass = [...change, oprationUser];
                state.UserRegistrationData = [...finalPass];
                localStorage.setItem("UserRegistrationData", JSON.stringify([...state.UserRegistrationData]));
                TosterSuccess("Password Updated !");
                unloadUser.redirection("/")
            }
            else {
                TosterError("Wrong Password");
            }
        },
        // this is the scope of the reducers //
    }

});

export const ProductAction = ProductSlice.actions;
