import React, { useState } from 'react';
import loginImage from '../Assets/images/login-2.svg';
import { Link, useNavigate } from 'react-router-dom';
import { useDispatch, useSelector, } from 'react-redux';
import { ProductAction } from '../Store/ProductSlice';

export const LoginForm = () => {
  const DISPATCH = useDispatch();
  const redirection = useNavigate();
  const initialUser2= {
    email:"",
    password:""
  }

  // const selectorData = useSelector((state)=>{ return state.Products})
  // console.log(selectorData);

  const [LoginUser,setLoginUser]=useState(initialUser2);

  const setLoginData=(e)=>
  {
    setLoginUser({...LoginUser,[e.target.name]:e.target.value});
  }

  const LoginSubmitData=(e)=>
  {
    e.preventDefault();
    DISPATCH(ProductAction.UserLogin({value:LoginUser,redirection:redirection}));
    setLoginUser(initialUser2);
  }

  // console.log(">>>>>>>>>",LoginUser);
  return (
    <div>
         <div className="container my-4">
          <div className="card">
          <section className="">
        <div className="container  ">
          <div className="row d-flex align-items-center justify-content-center  ">
            <div className="col-md-8 col-lg-7 col-xl-6 ">
              <img src={loginImage} className="img-fluid" alt="Phone image" />
            </div>
            <div className="col-md-7 col-lg-5 col-xl-6 px-3 px-md-5 ps-xl-3">
              <p className='tit-x mb-3 e-title'>Login</p>
              <form onSubmit={(e)=>{LoginSubmitData(e)}}>
                {/* Email input */}
                <div className=" mb-4">
                  <input type="email" id="form1Example13" className="form-control form-control-lg" placeholder='EMAIL' name="email" value={LoginUser.email} onChange={(e)=>{setLoginData(e)}}/>
                 
                </div>
                {/* Password input */}
                <div className=" mb-4">
                  <input type="password" id="form1Example23" className="form-control form-control-lg" placeholder='PASSWORD' name="password" value={LoginUser.password} onChange={(e)=>{setLoginData(e)}}/>
                 
                </div>
                <div className="d-flex justify-content-around align-items-center mb-4">
                  {/* Checkbox */}
                  <div className="form-check">
                    <input className="form-check-input" type="checkbox" defaultValue id="form1Example3" defaultChecked />
                    <label className="form-check-label" htmlFor="form1Example3"> Remember me </label>
                  </div>
                  {/* <a href="#!" className='color-x1'>Forgot password?</a> */}
                  <Link to="/forgot"  className='color-x1'>Forgot Password?</Link>
                </div>

                {/* Submit button */}
                <button type="submit" className="btn btn-x1  btn-lg btn-block">Sign in</button>
                <div className="d-flex mt-4"><Link to="registration" className='color-x1 m-auto'>don't have an account ?</Link></div>
                <div className="divider d-flex align-items-center my-3">
                  <p className="text-center fw-bold mx-3 mb-0 text-muted">OR</p>
                </div>
                <a className="btn btn-primary btn-lg btn-block shadow-0" style={{backgroundColor: '#3b5998'}} href="#!" role="button">
                  <i className="fab fa-facebook-f me-2 " />Continue with Facebook
                </a>
                <a className="btn btn-primary btn-lg btn-block shadow-0" style={{backgroundColor: '#55acee'}} href="#!" role="button">
                  <i className="fab fa-twitter me-2" />Continue with Twitter</a>
              </form>
            </div>
          </div>
        </div>
      </section>
          </div>
         </div>
    </div>
  )
}
