import React from 'react'
import { useSelector } from 'react-redux'



export const Tables = () => {

    const GetUserInfo = useSelector((state)=>{return state.Products.UserRegistrationData});
    
  return (
    <div>
        <div className="container my-4">
            <div className="card p-4">
            <div className="table-responsive ">
        <table className="table table-bordered ">
          <thead>
            <tr>
              <th className='btn-x1'>FirstName</th>
              <th className='btn-x1'>LastName</th>
              <th className='btn-x1'>DOB</th>
              <th className='btn-x1'>City</th>
              <th className='btn-x1'>State</th>
              <th className='btn-x1'>Address</th>
              <th className='btn-x1'>Email</th>
              <th className='btn-x1'>MoNo</th>
            </tr>
          </thead>
          <tbody>
           {GetUserInfo&&GetUserInfo.map((value,index)=>{
               return(
                 <tr key={index}>
                    <td>{value.firstname}</td>
                    <td>{value.lastname}</td>
                    <td>{value.DOB}</td>
                    <td>{value.city}</td>
                    <td>{value.states}</td>
                    <td>{value.address}</td>
                    <td>{value.email}</td>
                    <td>{value.mono}</td>
                 </tr>
               )
           })
           }
          </tbody>
        </table>
      </div>
            </div>
        </div>
    </div>
  )
}
