import React, { useContext, useEffect, useState } from 'react'
import { ProductContext } from '../Contexts/ProductContext'
import { SingalProductContext } from '../Contexts/SingalX';
import { useDispatch } from 'react-redux';
import { ProductAction } from '../Store/ProductSlice';
import { TosterSuccess } from './TostSuccess';

export const Products = (props) => {

  const DISPATCH = useDispatch();
  let ProductsData = useContext(ProductContext);
  const [product1,setProducts] = useState(null);
  let dePro = useContext(SingalProductContext);

 async function FetchProducts(){
          let FinalProducts = await ProductsData.products();
          setProducts(FinalProducts);
  }

  // console.log(product1);



useEffect(()=>{
       FetchProducts();
},[]);

  return (
    
    <div>
        <div className="container my-4">
        <div className="row row-cols-1 row-cols-md-2 row-cols-lg-3 row-cols-xl-4 p-4 p-sm-0 g-3">
        {product1 && product1.map((value,index)=>{
          return(
            <div className="col" key={index} >
          <div className="card" onClick={()=>{}}>
            <div className="d-flex justify-content-between p-3">
              <p className="lead mb-0">Today's Combo Offer</p>
              <div className="btn-x1 rounded-circle d-flex align-items-center justify-content-center shadow-1-strong" style={{width: '35px', height: '35px'}}>
                <p className="text-white mb-0 small">{index}</p>
              </div>
            </div>
            <div className="d-flex px-4">
            <img src={value.image} className="card-img-top m-auto width-x1"    alt="Laptop" />
            </div>
            <div className="card-body">
              
              <div className="d-flex justify-content-between mb-3 fst-x1" onClick={()=>{props.revPro();dePro.setDproduct(value)}}>
                <h5 className="mb-0">{value.category}</h5>
                <h5 className="text-dark mb-0">{value.price}</h5>
              </div>
              <div className="d-flex justify-content-between mb-2">
                <p className="text-muted mb-0">Available: <span className="fw-bold">{value.rating.count}</span></p>
                <div className="ms-auto color-x1">
                  {
                    ProductsData.Ratings(value.rating.rate).map((value)=>{
                            return(<i class="fa-solid fa-star"></i>)
                     })
                  }
                </div>
              </div>
              <div className="d-flex mb-1 justify-content-between px-1 mt-3 ">
              <i class="fa-regular fa-heart fst-x1"></i>
              <i class="fa-solid fa-basket-shopping fst-x1" onClick={()=>{DISPATCH(ProductAction.setStore({key:"ProductCart",value:value}));TosterSuccess(`${value.category} is Add in your cart`)}}></i>
              </div>
            </div>
          </div>
        </div>
          )
        })}
      
      </div>
        </div>
    </div>
  )
}
