import React, { useState } from 'react'
import { NevbarY } from '../Components/NevbarY'
import { FooterY } from '../Components/FooterY'
import { Heading } from '../Components/Heading'
import { Products } from '../Components/Products'
import { SingalProduct } from './SingalProduct'
import { SingalProductContext } from '../Contexts/SingalX'

export const Shop = () => {
    const [hPro, sethPro] = useState(false);
    const [Dproduct,setDproduct] = useState({})
    const revPro = () => { sethPro(!hPro) };
  return (
    <div>
        <NevbarY />
        <Heading title={"Shopping"}/>
      
        <SingalProductContext.Provider value={{
           Dproduct:Dproduct,
           setDproduct:setDproduct,
      }}>
        <Products revPro={revPro} />
        {hPro && <SingalProduct revPro={revPro} />}
      </SingalProductContext.Provider>
        <FooterY />
    </div>
  )
}
